import Video from './'
import Grid from './grid'
import Form from './form'
export default {
    name:'video',
    path:'video',
    meta:{alias:''},
    component:Video,
    redirect:{name:'video-grid'},
    subRoutes:[
        {
            name:'video-grid',
            path:'grid',
            meta:{alias:'',isForm:false,moduleDesc:'',dataDesc:''},
            component:Grid
        },{
            name:'video-form',
            path:'form',
            meta:{alias:'',isForm:true},
            component:Form
        }
    ]
}


