<template>
    <div :class="style.basicinf">
        <div :class="style.form">
            <div :class="style.basicinfLeft">
                <span></span>
            </div>
            <div :class="style.basicinfRight">
                <el-form ref="formData" :model="formData" :rules="rules">
                    <div :class="style.list_row">
                        <div :class="style.list_cell">
                            <div>
                                <div :class="style.field"></div>
                                <div :class="style.fieldValue">
                                    <el-form-item prop="DTMC">
                                        <el-input :readonly="state==EditState.READ" class="vali" v-model="formData.DTMC"></el-input>
                                    </el-form-item>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field"></div>
                                <div :class="style.fieldValue">
                                    <el-input :readonly="state==EditState.READ" class="content" readonly v-model="formData.ZZ" @focus="editAuthor"></el-input>
                                    <i class="el-icon-edit" @click="editAuthor"></i>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field"></div>
                                <div :class="style.fieldValue">
                                    <el-input :readonly="state==EditState.READ" class="content" v-model="formData.CBZ"></el-input>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field"></div>
                                <div :class="style.fieldValue">
                                    <el-input :readonly="state==EditState.READ" class="content" v-model="formData.CBG"></el-input>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field"></div>
                                <div :class="style.fieldValue">
                                    <el-form-item prop="ZZSJ">
                                        <el-input placeholder="2017" :readonly="state==EditState.READ" class="vali" v-model="formData.ZZSJ"></el-input>
                                    </el-form-item>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field"></div>
                                <div :class="style.fieldValue">
                                    <el-input placeholder="18401904" :readonly="state==EditState.READ" class="content" v-model="formData.TDSJ"></el-input>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field"></div>
                                <div :class="style.fieldValue">
                                    <el-form-item prop="TGDWMC">
                                        <el-autocomplete :disabled="state==EditState.READ" class="inline-input-vali" v-model="state2" :fetchSuggestions="querySearch" placeholder="" @select="handleSelect" :trigger-on-focus="false">
                                        </el-autocomplete>
                                    </el-form-item>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field"></div>
                                <div :class="style.fieldValue">
                                    <el-form-item prop="TGDWZB">
                                        <el-input readonly class="vali" v-model="formData.TGDWZB" @focus="editTGDWZB"></el-input>
                                        <i class="el-icon-edit" @click="editTGDWZB"></i>
                                        <i class="pointPickup" @click="toPickup"></i>
                                    </el-form-item>

                                </div>
                            </div>
                        </div>
                        <div :class="style.list_cell">
                            <div>
                                <div>
                                    <div :class="style.field"></div>
                                    <div :class="style.fieldValue">
                                        <el-input readonly class="content" placeholder="" v-model="formData.DZWJMC" @focus="editDZWJMC"></el-input>
                                        <i class="el-icon-edit" @click="editDZWJMC"></i>
                                    </div>
                                </div>
                                <div :class="style.field"></div>
                                <div :class="style.fieldValue">
                                    <el-form-item prop="YYID">
                                        <el-select multiple :disabled="state==EditState.READ" class="vali" v-model="formData.YYID">
                                            <el-option v-for="(item,index) in languageList" :key="`${index}`" :value="item.ID" :value-key="item.ID" :label="item.MC"></el-option>
                                        </el-select>
                                    </el-form-item>
                                </div>
                            </div>

                            <div>
                                <div :class="style.field"></div>
                                <div :class="style.fieldValue">
                                    <el-form-item prop="GJC">
                                        <el-input readonly class="vali" placeholder="" v-model="formData.GJC" @focus="editKeyword"></el-input>
                                        <i class="el-icon-edit" @click="editKeyword"></i>
                                    </el-form-item>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field"></div>
                                <div :class="style.fieldValue">
                                    <el-form-item prop="SQFWID">
                                        <el-select :disabled="state==EditState.READ" class="vali" v-model="formData.SQFWID">
                                            <el-option v-for="(item,index) in periodList" :key="`${index}`" :value="item.ID" :label="item.SQFW"></el-option>
                                        </el-select>
                                    </el-form-item>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field"></div>
                                <div :class="style.fieldValue">
                                    <el-form-item prop="SZZID">
                                        <el-cascader :disabled="state==EditState.READ" :value="value" class="vali" placeholder="" :options="regionList" filterable change-on-select @change="localHandleChange"></el-cascader>
                                    </el-form-item>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field"></div>
                                <div :class="style.fieldValue">
                                    <el-input class="content" readonly v-model="formData.DTGZDZBXX" @focus="editCoordinate"></el-input>
                                    <i class="el-icon-edit" @click="editCoordinate"></i>
                                    <i class="pointPickup" @click="toMultiPickup"></i>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field"></div>
                                <div :class="style.fieldValue">
                                    <el-input :readonly="state==EditState.READ" class="content" v-model="formData.ZBXT"></el-input>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field"></div>
                                <div :class="style.fieldValue">
                                    <el-form-item prop="SKD">
                                        <el-select :disabled="state==EditState.READ" class="vali" v-model="formData.SKD">
                                            <el-option v-for="(item,index) in skdList" :key="`${index}`" :value="item.id" :label="item.name"></el-option>
                                        </el-select>
                                    </el-form-item>
                                </div>
                            </div>
                        </div>
                        <div :class="style.list_cell">

                            <div>
                                <div :class="style.field"></div>
                                <div :class="style.fieldValue">
                                    <el-input :readonly="state==EditState.READ" class="content" v-model="formData.SBSDQ"></el-input>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field"></div>
                                <div :class="style.fieldValue">
                                    <el-input :readonly="state==EditState.READ" class="content" v-model="formData.BLC"></el-input>
                                </div>
                            </div>
                            <div>
                                <div>
                                    <div :class="style.field"></div>
                                    <div :class="style.fieldValue">
                                        <el-input :readonly="state==EditState.READ" class="content" v-model="formData.GCJZ"></el-input>
                                    </div>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field"></div>
                                <div :class="style.fieldValue">
                                    <el-input :readonly="state==EditState.READ" class="content" v-model="formData.DTTY"></el-input>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field"></div>
                                <div :class="style.fieldValue">
                                    <el-input :readonly="state==EditState.READ" class="content" v-model="formData.DTJDMC"></el-input>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field"></div>
                                <div :class="style.fieldValue">
                                    <el-input :readonly="state==EditState.READ" class="content" v-model="formData.WZ"></el-input>
                                </div>
                            </div>
                            <div>
                                <div :class="style.field"></div>
                                <div :class="style.fieldValue">
                                    <el-input v-if="isVisable" :readonly="this.state==EditState.READ" class="content" placeholder="" v-model="formData.FJLJ">
                                        <template slot="prepend">{{url}}</template>
                                    </el-input>
                                    <el-input v-else class="content" readonly v-model="formData.FJLJ">
                                    </el-input>
                                    <!-- <i class="el-icon-edit" @click="addAddress"></i> -->
                                </div>
                            </div>
                            <div>
                                <div :class="style.field"></div>
                                <div :class="style.fieldValue">
                                </div>
                            </div>
                        </div>
                    </div>
                    <div>
                        <div>
                            <div :class="style.fieldBottom"></div>
                            <div :class="style.fieldValueBottom">
                                <el-form-item prop="MS">
                                    <el-input :readonly="state==EditState.READ" class="fieldValueBottom" type="textarea" v-model="formData.MS" placeholder="  "></el-input>
                                </el-form-item>
                            </div>
                        </div>
                        <div>
                            <div :class="style.fieldBottom"></div>
                            <div :class="style.fieldValueBottom">
                                <el-input :readonly="state==EditState.READ" class="fieldValueBottom" type="textarea" placeholder="" v-model="formData.BZ"></el-input>
                            </div>
                        </div>
                        <div>
                            <div :class="style.pic"></div>
                            <div :class="style.picBottom">
                                <!-- <div></div> -->
                                <!-- <div> -->
                                <!-- <el-button :class="style.picbtn" type="success"></el-button> -->

                                <el-upload class="avatar-uploader" v-if="showSLT" :disabled="state==EditState.READ" :action="UPLOADURL" :show-file-list="false" :on-success="handleAvatarSuccess">
                                    <img v-if="imageUrl" :src="imageUrl" class="avatar">
                                    <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                </el-upload>

                                <!-- </div> -->
                            </div>
                        </div>
                    </div>
                </el-form>
            </div>
        </div>
        <div :class="style.btndiv" v-if="state!==EditState.READ">
            <el-button class="sureBtn" @click="validate('formData')">
                
            </el-button>
            <el-button class="cancelBtn" @click="cancelBtnHandler('formData')">
                
            </el-button>
        </div>
        <map-coordinate :mapCoordinateData="mapCoordinateData" :dialogFormVisible="dialogFormVisible" @save="save" @cancel="cancel" :mapCoorFooterVisible="mapCoorFooterVisible"></map-coordinate>
        <company-Coordinate :mapCoordinateData="locData" :dialogFormVisible="locFormVisible" @save="saveTGDWZB" @cancel="cancel" :mapCoorFooterVisible="mapCoorFooterVisible"></company-Coordinate>
        <author-msg :authorData="authorData" :authorDialogVisible="authorDialogVisible" @save="saveAuthor" @cancel="cancel" :autherFooterVisible="autherFooterVisible"></author-msg>
        <keyword-dialog :keywordDialogVisible="keywordDialogVisible" :keywordData="keywordData" @save="saveKeyword" @cancel="cancelKeyword" :keywordFooterVisible="keywordFooterVisible"></keyword-dialog>
        <electronfile-dialog :electronfiledDialogVisible="electronfiledDialogVisible" :electronFileData="electronFileData" :electronFileFooterVisible="electronFileFooterVisible" @save="saveElectronfile" @cancel="cancelElectronfile"></electronfile-dialog>
        <point-pickup v-if="pickupFormVisible" :point="point" :allowPickup="allowPickup" @cancel="cancelbtn" @save="savebtn"></point-pickup>
        <point-pickup-multi v-if="multiPickupFormVisible" :points="points" :allowPickup="allowPickup" @cancel="cancelbtn" @save="save"></point-pickup-multi>
    </div>
</template>

<script>
import { DOWNLOADURL, UPLOADURL } from '@/configs/upload-download'
import style from '@/css/map-form.css'
import * as  EditState from '@/enums/edit-state'
import OM_MAP from '@/models/entity/OM_MAP'
import { mapActions } from 'vuex'
import { getCompanyData, getLanguageList, getPeriodList, getRegionList } from './mutation-types'
import { edittMapData, seeMapData, putMapData } from './mutation-types'
import MapCoordinate from '#com/map-coordinate'
import companyCoordinate from '#com/company-coordinate'
import AuthorMsg from '#com/author-msg'
import { rules } from '@/util/rules.js'
import KeywordDialog from '#com/keyword-dialog'
import electronfileDialog from '#com/electronfile-dialog'
import PointPickup from '#com/point-map-pickup'
import PointPickupMulti from '#com/point-map-pickup-multi'
export default {
    name: 'map-form',
    data() {
        return {
            style,
            state: null,
            state2: '',
            EditState,
            formId: null,
            formData: new OM_MAP(),
            mapCoordinateData: null,
            dialogFormVisible: false,
            mapCoorFooterVisible: true,
            locData: null,
            locFormVisible: false,
            authorData: null,
            authorDialogVisible: false,
            autherFooterVisible: true,
            tmList: [{ id: '0', name: '' }, { id: '1', name: '' }],
            skdList: [{ id: '0', name: '' }, { id: '1', name: '' }],
            companyListData: [],
            languageList: [],
            periodList: [],
            regionList: [],
            value: [],
            rules: rules,
            url: '',
            currentName: 'map',
            isVisable: true,
            DOWNLOADURL,
            UPLOADURL,
            imageUrl: '',
            imgPath: '',
            //
            keywordDialogVisible: false,
            keywordData: "",
            keywordFooterVisible: true,
            //
            electronfiledDialogVisible: false,
            electronFileData: "",
            electronFileFooterVisible: true,
            //
            pickupFormVisible: false,
            point: null,
            allowPickup: true,
            showSLT: true,
            multiPickupFormVisible: false,
            points: null,
        }
    },
    methods: {
        ...mapActions({
            seeMapData,
            putMapData,
            edittMapData,
            getCompanyData,
            getLanguageList,
            getPeriodList,
            getRegionList
        }),
        //
        async saveData() {
            this.formData.LB = '0';//0
            let form = new OM_MAP(this.formData);
            //
            form.GJC = form.GJC.replace(//ig, ',');
            form.DZWJMC = form.DZWJMC.replace(//ig, ';');
            //TGDWZB
            if (form.TGDWZB) {
                let arr = form.TGDWZB.split(",");
                let lon = arr[1].slice(arr[1].length - 1, arr[1].length);
                let lat = arr[0].slice(arr[0].length - 1, arr[0].length)
                form.TGDWZB = this.covertDegree(arr[1], lon) + ',' + this.covertDegree(arr[0], lat);
            }
            if (!this.state2) {
                form.TGDWMC = "";
                form.TGDWID = 1;
                form.TGDWZB = "";
            }
            // if (form.TGDWMC && this.state2) {
            //     if (form.TGDWMC != this.state2) {
            //         form.TGDWMC = this.state2;
            //         form.TGDWID = 0;
            //     }
            // }
            if (form.FJLJ != "" && this.isVisable) {
                form.FJLJ = this.url + form.FJLJ;
            }
            if (form.YYID) {
                form.YYID = this.createyyFilter(form.YYID);
            }
            form.SLT = this.imgPath;
            let jsonData = JSON.stringify(form);
            if (this.state == EditState.ADD) {
                let result = await this.putMapData(jsonData);
                if (!result) return this.$message({ showClose: true, message: '', type: 'error' });
                this.$message({ showClose: true, message: '', type: 'success' });
                this.$router.go(-1);
            }
            if (this.state == EditState.EDIT) {
                let result = await this.edittMapData(jsonData);
                if (!result) return this.$message({ showClose: true, message: '', type: 'error' });
                this.$message({ showClose: true, message: '', type: 'success' });
                this.$router.go(-1);
            }
        },
        createyyFilter(queryArray) {
            let filterlgArr = [];
            for (let i = 0; i < queryArray.length; i++) {
                let id = queryArray[i];
                for (let m = 0; m < this.languageList.length; m++) {
                    if (this.languageList[m].ID == id) {
                        filterlgArr.push(this.languageList[m]);
                    }

                }
            }
            return filterlgArr;
        },
        //formIdformData
        async getFormData() {
            let result = await this.seeMapData(this.formId);
            let form = result;
            this.point = form.TGDWZB;
            if (form.hasOwnProperty('DTGZDZBXX')) {
                this.points = form.DTGZDZBXX;
            }
            if (form && form.hasOwnProperty('TGDWMC') && form.hasOwnProperty('TGDWZB')) {
                this.state2 = form.TGDWMC;
                //TGDWZB
                if (form.TGDWZB) {
                    let arr = form.TGDWZB.split(',');
                    form.TGDWZB = this.formatDegreeBack(arr[1], 'lat') + ',' + this.formatDegreeBack(arr[0], 'lon');
                }
            }
            if (form.SZZID && form.SZGJID) {
                this.value = [form.SZZID, form.SZGJID];
            } else if (form.SZZID && !form.SZGJID) {
                this.value = [form.SZZID];
            }
            if (!form.FJLJ) {
                this.isVisable = true;
            } else {
                this.isVisable = false;
            }
            if (form.YYID)
                form.YYID = form.YYID.map(item => item.ID);
            if (form.SLT)
                this.imageUrl = this.DOWNLOADURL + form.SLT;
            if (form.SLT == "" && this.state == EditState.READ)
                this.showSLT = false;
            this.formData = new OM_MAP(form);
        },
        //
        querySearch(queryString, cb) {
            var companyListData = this.companyListData;
            var results = queryString ? companyListData.filter(this.createFilter(queryString)) : companyListData;
            //  callback 
            cb(results);
        },
        createFilter(queryString) {
            return (companyListData) => {
                if (companyListData.value) {
                    return (companyListData.value.indexOf(queryString.toLowerCase()) != -1);
                }
            };
        },
        //
        handleSelect(item) {
            // this.formData.TGDWID = item.id;
            // this.formData.TGDWMC = item.value;
            // //item.loc
            // let arr = item.loc.split(',');
            // this.formData.TGDWZB = this.formatDegreeBack(arr[1], 'lat') + ',' + this.formatDegreeBack(arr[0], 'lon');
            this.focusout();
        },
        focusout() {
            if (!this.state2) { this.formData.TGDWZB = ""; return; }
            let currenttgdw = this.companyListData.find(item => item.value == this.state2);
            if (!currenttgdw) {
                this.formData.TGDWMC = this.state2;
                this.formData.TGDWID = 0;
            }
            else {
                this.formData.TGDWMC = this.state2;
                this.formData.TGDWID = currenttgdw.id;
                if (currenttgdw.loc) {
                    let arr = currenttgdw.loc.split(',');
                    this.formData.TGDWZB = this.formatDegreeBack(arr[1], 'lat') + ',' + this.formatDegreeBack(arr[0], 'lon');
                }
            }
        },
        //
        localHandleChange(item) {
            //itemthis.formData.SZZID;this.formData.SZGJID
            if (item.length == 1) {
                this.formData.SZZID = item[0];
                this.formData.SZGJID = "";
            } else if (item.length == 2) {
                this.formData.SZZID = item[0];
                this.formData.SZGJID = item[1];
            }
        },
        //
        async getcompanyList() {
            let companyData = await this.getCompanyData();
            if (!companyData) return;
            this.companyListData = companyData.map(item => {
                return { value: item.MC, id: item.ID, loc: item.TGDWZB };
            });
            // this.companyListData = companyData;
        },
        //
        async getlanguageData() {
            let languageData = await this.getLanguageList();
            if (!languageData) return;
            this.languageList = languageData;
        },
        //
        async getPeriodData() {
            let periodData = await this.getPeriodList();
            if (!periodData) return;
            this.periodList = periodData;
        },
        //
        async getRegionData() {
            let regionData = await this.getRegionList();
            if (!regionData) return;
            regionData.forEach(function(item) {
                if (item.hasOwnProperty('CHILDREN')) {
                    item.CHILDREN = item.CHILDREN.map(item1 => {
                        return { value: item1.ID, label: item1.MC };
                    });
                }
            });
            this.regionList = regionData.map(item => {
                return { value: item.ID, label: item.MC, children: item.CHILDREN };
            });
        },
        //
        showSuccess() {
            this.$message({
                message: '',
                type: 'success'
            });
        },
        //
        showError() {
            this.$message({
                message: '',
                type: 'warning'
            });
        },
        //
        editCoordinate() {
            if (this.state == EditState.READ) this.mapCoorFooterVisible = false;
            else this.mapCoorFooterVisible = true;
            this.dialogFormVisible = true;
            this.mapCoordinateData = this.formData.DTGZDZBXX;
        },
        //
        editAuthor() {
            if (this.state == EditState.READ) this.autherFooterVisible = false;
            else this.autherFooterVisible = true;
            this.authorDialogVisible = true;
            this.authorData = this.formData.ZZ;
        },
        //
        editKeyword() {
            if (this.state == EditState.READ)
                this.keywordFooterVisible = false;
            else
                this.keywordFooterVisible = true;
            this.keywordDialogVisible = true;
            this.keywordData = this.formData.GJC;
        },
        //
        saveKeyword(gjcdata) {
            this.keywordDialogVisible = false;
            if (!gjcdata) return;
            this.formData.GJC = gjcdata;

        },
        //dialog
        cancelKeyword() {
            this.keywordDialogVisible = false;
        },
        //
        editDZWJMC() {
            if (this.state == EditState.READ) this.electronFileFooterVisible = false;
            else this.electronFileFooterVisible = true;
            this.electronfiledDialogVisible = true;
            this.electronFileData = this.formData.DZWJMC;
        },
        //
        saveElectronfile(dzwjmcdata) {
            if (!dzwjmcdata) return;
            this.electronfiledDialogVisible = false;
            this.formData.DZWJMC = dzwjmcdata;
        },
        //dialog
        cancelElectronfile() {
            this.electronfiledDialogVisible = false;
        },
        editTGDWZB() {
            if (this.state == EditState.READ) this.mapCoorFooterVisible = false;
            else this.mapCoorFooterVisible = true;
            this.locFormVisible = true;
            this.locData = this.formData.TGDWZB;
        },
        //dialog
        saveAuthor(param) {
            this.authorDialogVisible = false;
            this.formData.ZZ = param;
        },
        saveTGDWZB(param) {
            this.locFormVisible = false;
            this.formData.TGDWZB = param;
        },
        cancel() {
            this.dialogFormVisible = false;
            this.authorDialogVisible = false;
            this.locFormVisible = false;
        },
        save(param) {
            this.dialogFormVisible = false;
            this.multiPickupFormVisible = false;
            this.formData.DTGZDZBXX = param;
            this.points = param;
        },
        validate(formName) {
            let val;
            this.$refs[formName].validate((valid) => {
                val = valid;
                if (valid) {
                    this.saveData();
                } else {
                    return false;
                }
            });
            if (val) return;
            this.$notify({
                title: '',
                message: "",
                duration: 3000,
                type: 'info',
                offset: 125,
            });
        },
        cancelBtnHandler(formName) {
            this.formData = new OM_MAP();
            this.$refs[formName].resetFields();
        },
        //
        sureOrCancelVis() {
            let btndiv = document.getElementById('twoBtn');
            btndiv.style.display = "none";
        },
        // 
        handleAvatarSuccess(res, file) {
            this.imgPath = res;
            this.imageUrl = URL.createObjectURL(file.raw);
        },
        //
        cancelbtn() {
            this.pickupFormVisible = false;
            this.multiPickupFormVisible = false;
        },
        savebtn(param) {
            this.formData.TGDWZB = param;
            this.pickupFormVisible = false;
        },
        //
        toPickup() {
            if (this.state == EditState.READ)
                this.allowPickup = false;
            this.pickupFormVisible = true;

        },
        toMultiPickup() {
            this.multiPickupFormVisible = true;
            if (this.state == EditState.READ)
                this.allowPickup = false;

        },
    },
    components: {
        MapCoordinate,
        AuthorMsg,
        companyCoordinate,
        KeywordDialog,
        electronfileDialog,
        PointPickup,
        PointPickupMulti
    },
    created() {
        //formIdform
        let { state, id, name } = this.$route.query;
        this.formId = id;
        this.state = state;
        this.getRegionData();
        //
        if (this.state != EditState.ADD) this.getFormData();

        this.mapCoordinateData = this.formData.DTGZDZBXX;
        this.authorData = this.formData.ZZ;
        this.url = `${this.currentName}/${this.getNowFormatDate()}/`;
    },
    mounted() {
        this.getcompanyList();
        this.getlanguageData();
        this.getPeriodData();
        this.$el.getElementsByClassName('inline-input-vali')[0].querySelector("input").onblur = this.focusout;//
        // if (this.state == "read") {
        //     this.sureOrCancelVis();
        // }
    }
}
</script>




