import Image from './'
import Grid from './grid'
import Form from './form'
export default {
    name:'image',
    path:'image',
    meta:{alias:''},
    component:Image,
    redirect:{name:'image-grid'},
    subRoutes:[
        {
            name:'image-grid',
            path:'grid',
            meta:{alias:'',isForm:false,moduleDesc:'',dataDesc:''},
            component:Grid
        },
        {
            name:'image-form',
            path:'form',
            meta:{alias:'',isForm:true},
            component:Form
        }
    ]
}


