import User from './'
import Grid from './grid'
import Form from './form'
export default {
    name:'user',
    path:'user',
    meta:{alias:''},
    component:User,
    redirect:{name:'user-grid'},
    subRoutes:[
        {
            name:'user-grid',
            path:'grid',
            meta:{alias:'',isForm:false,moduleDesc:'',dataDesc:''},
            component:Grid
        },{
            name:'user-form',
            path:'form',
            meta:{alias:'',isForm:true},
            component:Form
        }
    ]
}


