import Article from './'
import Grid from './grid'
import Form from './form'
export default {
    name:'article',
    path:'article',
    meta:{alias:''},
    component:Article,
    redirect:{name:'article-grid'},
    subRoutes:[
        {
            name:'article-grid',
            path:'grid',
            meta:{alias:'',isForm:false,moduleDesc:'',dataDesc:''},
            component:Grid
        },
        {
            name:'article-form',
            path:'form',
            meta:{alias:'',isForm:true},
            component:Form
        }
    ]
}


