<template>
    <div :class="style.backcolor">
        <div id="mydiv" :class="style.convas"></div>
        <div>
           <!-- <div :class="style.title">
           </div> -->
           <!-- <div class="login-form"> -->
                <log-form :class="style.form" ref="form"></log-form>
           <!-- </div> -->
        </div>
       <!--  <div :class="style.footer">
            <div><a href="http://www.gly.cn/Index.htm"></a></div>
            <div :class="style.space"><a href="http://www.geo-compass.com/"></a></div>
        </div> -->
    </div>
</template>
<script>
// import CanvasParticle from 'canvas-particle'
import style from '@/css/login.css'
import {login,getSession,SET_USER} from './mutation-types'
import {mapActions} from 'vuex'
import LogForm from './form'
export default {
   name:'login',
   data(){
       return {
           style,
           config:{
            vx: 4,  //x,
            vy: 4,  //y
            height: 2,  //
            width: 2,
            count: 500,   //
            color: "121, 162, 185",   //
            stroke: "130,255,255",    //
            dist: 6000,   //
            e_dist: 20000,  //
            max_conn: 10  //
           }
       };       
   },
   components:{
       LogForm
   },
   methods:{
       ...mapActions({
           login,
           getSession
       })
   },
   created(){

   },
   mounted(){
       //
    console.log(this.config);
        CanvasParticle(this.config);

   }
}
</script>




